import VueRouter from "vue-router"
import vqnalist from "../components/qnalist"
import detail from "../components/detail"
import insert from "../components/insert"
import updateBoard from "../components/update"

const router = new VueRouter({
    // 애플리케이션에서 사용되는 모든 경로 정보를 저장하는 배열
    routes:[
        // {
            // name:"경로에 대한 예명",
            // path:"요청하는 경로",
            // component:"표시해줄 컴포넌트"
        // }
        {name:"vqnalist", path:"/", component:vqnalist},
        {name:"detail", path:"/detail/:no", component:detail, props:true},
        {name:"insert", path:"/insert", component:insert},
        {name:"updateBoard", path:"/updateBoard/:no", component:updateBoard, props:true},
    ]
})

export default router