<template>
  <div id="detail">
    <div id="result" class="container">
      <article id="mainContent">
        <div class="main" id="viewBoard">
            <input type="hidden" name="no" id="no" value="board.no" />

            <input type="hidden" name="query" id="query" />
            <table class="table">
              <tr>
                <th colspan="4" class="high_light text-left">게시글 보기</th>
              </tr>
              <tbody>
                <tr>
                  <td width="100">
                    <label for="title" class="high_light">제목</label>
                  </td>
                  <td colspan="3">{{board.title}}</td>
                </tr>
                <tr>
                  <td class="high_light">글쓴이</td>
                  <td>{{board.id}}</td>
                  <td class="high_light">게시일</td>
                  <td>{{board.regdate}}</td>
                </tr>
                <tr>
                  <td class="text-left" colspan="4" valign="top" height="200">{{board.contents}}</td>
                </tr>
              </tbody>
              <tfoot>
                <tr>
                  <td colspan="4" align="center">
                    <button @click="qnalist" class="btn btn-default">목록</button>
                    <button @click="updateBoard(board.no)" class="btn btn-success">수정</button>
                    <button @click="deleteBoard(board.no)" class="btn btn-danger">삭제</button>
                  </td>
                </tr>
              </tfoot>
            </table>
        </div>
      </article>
    </div>
  </div>
</template>

<script>
import router from "../assets/router";
import axios from "../js/http-commons.js";

export default {
  name: "detail",
  props: ["no"],
  data() {
    return {
      board: {}
    };
  },
  mounted() {
    console.log("ddd");
    axios
      .get("/QnABoard/" + this.no)
      .then(res => {
        this.board = res.data.data;
      })
      .catch(e => {
        console.log(e);
      })
      .finally(() => {
        console.log("가져옴");
      });
  },
  methods: {
    qnalist(){
      router.push({
        path:"/"
      })
    },
    updateBoard(no){
      console.log(no);
      router.push({
        path:"/updateBoard/"+no
      })
    },
    deleteBoard(no){
      axios.delete("/QnABoard/"+ no)
      .then(res => {
        console.log(res.data.status);
        router.push({
          path:"/"
        })
      }).catch(e=>{
        console.log(e);
      })
    }
  },
};
</script>

