<template>
  <div>
    <div class="container">
      <h1 class="sub-header">Q&A 작성</h1>
      <form>
        <div class="form-group">
          <label for="title">아이디</label>
          <input type="text" class="form-control" name="id" id="id" v-model="qna.id" />
        </div>
        <div class="form-group">
          <label for="title">제목</label>
          <input type="text" class="form-control" name="title" id="title" v-model="qna.title" />
        </div>
        <div class="form-group">
          <label for="contents">내용</label>
          <textarea
            name="contents"
            class="form-control"
            id="contents"
            cols="50"
            rows="7"
            v-model="qna.contents"
          ></textarea>
        </div>
        <div class="form-group">
          <input type="button" class="btn btn-default" @click="addqna" value="작성" />
          <input
            type="button"
            class="btn btn-default"
            value="취소"
            id="cancel"
            onclick="history.back()"
          />
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import axios from "../js/http-commons";
import router from "../assets/router";
export default {
  data() {
    return {
      qna: {}
    };
  },
  methods: {
    addqna() {
      console.log("qna", this.qna);
      axios
        .post("QnABoard", this.qna)
        .then(res => {
          console.log(res);
          router.push({
            path: "/"
          });
        })
        .catch(error => {
          console.log(error);
          alert("실패");
        });
    }
  }
};
</script>
<style>
body {
  font-family: "Roboto Slab", "Noto Sans KR";
}
</style>