<template>
  <div id="app">
    <div class="container">
      <h1 class="sub-header">Q&A</h1>
      <div class="table-responsive">
        <table class="table table-striped">
          <thead>
            <tr>
              <th width="70px">번호</th>
              <th>제목</th>
              <th width="120px">작성자</th>
              <th width="150px">작성시간</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="board in boards" :key="board.no" @click="detail(board.no)">
              <td>{{board.no}}</td>
              <td>{{board.title}}</td>
              <td>{{board.id}}</td>
              <td>{{board.regdate}}</td>
            </tr>
          </tbody>
        </table>
      </div>

      <div>
        <span>
          <a class="btn btn-default" @click="qnainsert">글쓰기</a>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import router from "../assets/router";
import axios from "../js/http-commons.js";

export default {
  data() {
    return {
      boards: []
    };
  },
  mounted() {
    axios
      .get("/QnABoard")
      .then(res => {
        this.boards = res.data.data;
      })
      .catch(e => {
        console.log(e);
      })
      .finally(() => {
        console.log("가져옴");
      });
  },
  methods: {
    detail(id) {
      console.log(id);
      router.push({
        path: "/detail/" + id
      });
    },
    qnainsert() {
      router.push({
        path: "/insert"
      });
    }
  }
};
</script>

